.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;

  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;
}

.modalWrapper {
  position: absolute;
  z-index: 2;
  transform: translate(-50%, 0);

  width: 100px;
  max-height: 150px;

  background-color: var(--c-rgba14614314309);

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  padding: 5px;

  border-radius: 15px;

  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);

  overflow-y: auto;
  overflow-x: hidden;

  @media screen and (min-width: 768px) {
    width: 130px;
  }

  @media (min-width: 1440px) {
    width: 170px;
  }
}

.modalBtnClose {
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 3;

  width: 10px;
  height: 10px;

  border: none;

  cursor: pointer;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    transform: scale(1.2);
  }
}

.modalBox {
  text-align: left;
  margin: 0 auto;
}

.modalText {
  color: var(--c-52555f);

  font-weight: 500;
  font-size: 14px;
  line-height: 1.143;
  letter-spacing: 0.02em;

  margin: 50px 0 20px;
}
